import { component } from 'picoapp';
import $ from 'jquery';

const selectors = {
  optionsEls: '[data-single-option-selector]',
  originalSelectEl: '[name="id"]'
};

class VariantSelectorRadio {
  constructor(el, ctx) {
    this.el = el;
    this.ctx = ctx;
    this.init();
    this.bindEvents();
  }

  init() {
    this.optionId = `#option${this.el.dataset.option}`;
    this.options = this.el.querySelectorAll(selectors.optionsEls);
    this.selector = this.el.querySelector(this.optionId);
  }

  bindEvents() {
    this.options.forEach(opt => {
      opt.addEventListener('change', (e) => {
        this.selector.value = e.target.value;
        this.selector.dispatchEvent(new Event('change'));

        //Jquery change event trigger for istock notifications 
        $(this.selector).trigger('change');
      })
    });
  }

}

export default component((node, ctx) => {
  new VariantSelectorRadio(node, ctx);
});
